import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { InputMoney, SelectAjax } from 'components/common/index';
import { Row, Card, Form, Input, InputNumber } from 'antd';
import {
  getDormitoryRoomById,
  updateDormitoryRoom,
  createDormitoryRoom,
} from 'services/dormitoryRoom';
import { getDormitorys } from 'services/dormitory';
import { getDormitory_DormitoryFloors } from 'services/dormitory_DormitoryFloor';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    dorm_FloorId: '',
    roomNo: '',
    size: 100,
    device: '',
    feePerPerson: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormDormitoryRoomPage' })
class FormDormitoryRoomPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getDormitoryRoomById({ id });
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createDormitoryRoom(values);
      } else {
        await updateDormitoryRoom({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    const dormitoryId = getFieldValue('dormitoryId');
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} phòng KTX`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item hasFeedback label="Tòa KTX" {...formItemLayout}>
              {getFieldDecorator('dormitoryId', {
                initialValue: initialValue.dormitoryId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<SelectAjax service={getDormitorys} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tầng" hasFeedback>
              {getFieldDecorator('dorm_FloorId', {
                initialValue: initialValue.dorm_FloorId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<SelectAjax service={getDormitory_DormitoryFloors} keyLabel="floorId" />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số phòng" hasFeedback>
              {getFieldDecorator('roomNo', {
                initialValue: initialValue.roomNo,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Sức chứa" hasFeedback>
              {getFieldDecorator('size', {
                initialValue: initialValue.size,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputNumber min={0} max={100} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Thiết bị">
              {getFieldDecorator('device', {
                initialValue: initialValue.device,
              })(<Input.TextArea autosize />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số tiền" hasFeedback>
              {getFieldDecorator('feePerPerson', {
                initialValue: initialValue.feePerPerson,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<InputMoney min={0} style={{ width: 200 }} />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormDormitoryRoomPage {...props} />
  </Page>
));



