import { request } from 'utils';

const prefix = '/api/categories';

export const getDormitory_DormitoryFloors = ({
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/Dormitory_DormitoryFloor`,
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createDormitory_DormitoryFloor = ({ dormId, floorId } = {}) =>
  request({
    prefix,
    url: `/Dormitory_DormitoryFloor`,
    method: 'POST',
    params: {},
    data: { dormId, floorId },
  });

export const getDormitory_DormitoryFloor = ({ id } = {}) =>
  request({
    prefix,
    url: `/Dormitory_DormitoryFloor/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const updateDormitory_DormitoryFloor = ({ id, dormId, floorId } = {}) =>
  request({
    prefix,
    url: `/Dormitory_DormitoryFloor/${id}`,
    method: 'PUT',
    params: {},
    data: { id, dormId, floorId },
  });

export const deleteDormitory_DormitoryFloor = ({ id } = {}) =>
  request({
    prefix,
    url: `/Dormitory_DormitoryFloor/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



